import * as yup from "yup";

export const planFormScheme = yup.object().shape({
    topic: yup.string().required(" "),
    duration: yup.number().required(" ").positive(" "),
    peoplePlanned: yup
        .number()
        .required(" ")
        .moreThan(0, " : 1")
        .integer("   "),
    date: yup.string().required(" "),
    teacher: yup.string().required(" "),
});
